@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,800);
@import url(https://code.ionicframework.com/ionicons/2.0.1/css/ionicons.min.css);
body {
  background-color: #222831; }

.character-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap; }

.character-wrapper {
  margin: 0 auto;
  max-width: 900px; }

.character-figure {
  background-color: #000000;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
  color: #ffffff;
  font-family: 'Open Sans', Arial, sans-serif;
  font-size: 16px;
  line-height: 1.6em;
  margin: 10px;
  max-width: 240px;
  min-width: 250px;
  overflow: hidden;
  position: relative;
  text-align: left;
  width: 100%; }

.character-figure * {
  box-sizing: border-box;
  transition: all 0.25s ease; }

.character-figure img {
  max-width: 100%;
  vertical-align: top;
  position: relative;
  opacity: 0.75; }

.character-figure figcaption {
  padding: 25px 20px 25px;
  position: absolute;
  bottom: 0;
  z-index: 1; }

.character-figure .tag {
  background-color: #fff;
  color: #333;
  font-size: 18px;
  font-weight: 800;
  min-height: 48px;
  min-width: 48px;
  padding: 10px 0;
  position: absolute;
  right: 15px;
  text-align: center;
  top: 0; }

.character-figure .tag span {
  display: block;
  line-height: 14px; }

.character-figure .tag span {
  font-size: 11px; }

.character-figure .tag .name {
  font-style: bold; }

.character-figure h3,
.character-figure p {
  margin: 0;
  padding: 0; }

.character-figure h3 {
  font-weight: 800;
  letter-spacing: -0.4px; }

.character-figure .hover {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  -webkit-align-items: center;
          align-items: center;
  background-color: rgba(236, 188, 46, 0.75);
  display: -webkit-flex;
  display: flex;
  font-size: 65px;
  -webkit-justify-content: center;
          justify-content: center;
  opacity: 0; }

.character-figure a {
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  position: absolute;
  z-index: 1; }

.character-figure:hover .hover,
.character-figure.hover .hover {
  transition-delay: 0.1s;
  opacity: 1; }

.character-figure:hover figcaption,
.character-figure.hover figcaption {
  opacity: 0; }

.character-figure:hover .tag,
.character-figure.hover .tag {
  -webkit-transform: translateY(-100%);
  transform: translateY(-100%); }

header h1 {
  text-align: center;
  font-size: 3.7em;
  margin-top: .4em;
  margin-bottom: .2em;
  font-style: italic;
  font-family: "IBM Plex Mono ExtraLight";
  color: #efbb35;
  font-weight: 200; }

.c-wrapper {
  height: 420px;
  width: 654px;
  margin: 50px auto;
  border-radius: 7px 7px 7px 7px;
  box-shadow: 0px 14px 32px 0px rgba(0, 0, 0, 0.15); }

.product-img {
  float: left;
  height: 420px;
  width: 327px; }

.product-img img {
  border-radius: 7px 0 0 7px; }

.product-info {
  float: left;
  height: 420px;
  width: 327px;
  border-radius: 0 7px 10px 7px;
  background-color: #ffffff; }

.product-text {
  height: 300px;
  width: 327px; }

.product-text h1 {
  margin: 0 0 0 38px;
  font-style: italic;
  font-family: "IBM Plex Mono ExtraLight";
  padding-top: 52px;
  font-size: 34px;
  color: #474747; }

.product-text h1,
.product-price-btn p {
  font-family: 'Bentham', serif; }

.product-text h2 {
  margin: 0 0 47px 38px;
  font-size: 13px;
  font-family: 'Raleway', sans-serif;
  font-weight: 400;
  color: #d2d2d2;
  letter-spacing: 0.2em; }

.product-text p {
  height: 125px;
  margin: 0 0 0 38px;
  font-family: 'Playfair Display', serif;
  color: #8d8d8d;
  line-height: 1.7em;
  font-size: 15px;
  font-weight: lighter;
  overflow: hidden; }

.product-price-btn {
  height: 103px;
  width: 327px;
  margin-top: 17px;
  position: relative; }

.product-price-btn p {
  display: inline-block;
  position: absolute;
  top: -13px;
  height: 50px;
  font-family: 'Trocchi', serif;
  margin: 0 0 0 38px;
  font-size: 28px;
  font-weight: lighter;
  color: #474747; }

.product-price-btn button {
  float: right;
  display: inline-block;
  height: 50px;
  width: 176px;
  margin: 0 40px 0 16px;
  box-sizing: border-box;
  border: transparent;
  border-radius: 60px;
  font-family: 'Raleway', sans-serif;
  font-size: 14px;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.2em;
  color: #ffffff;
  background-color: #9cebd5;
  cursor: pointer;
  outline: none; }

.product-price-btn button:hover {
  background-color: #79b0a1; }

div.backBtn {
  width: 100px;
  top: 18px;
  background-color: #f4f4f4;
  transition: all 0.4s ease;
  position: absolute;
  cursor: pointer; }

span.line {
  bottom: auto;
  right: auto;
  top: auto;
  left: auto;
  background-color: #8D8D8D;
  border-radius: 10px;
  width: 100%;
  left: 0px;
  height: 2px;
  display: block;
  position: absolute;
  transition: width 0.2s ease 0.1s, left 0.2s ease, background-color 0.2s ease, -webkit-transform 0.2s ease 0.3s;
  transition: width 0.2s ease 0.1s, left 0.2s ease, transform 0.2s ease 0.3s, background-color 0.2s ease;
  transition: width 0.2s ease 0.1s, left 0.2s ease, transform 0.2s ease 0.3s, background-color 0.2s ease, -webkit-transform 0.2s ease 0.3s; }

span.tLine {
  top: 0px; }

span.mLine {
  top: 13px;
  opacity: 0; }

span.bLine {
  top: 26px; }

.label {
  position: absolute;
  left: 0px;
  top: 5px;
  width: 100%;
  text-align: center;
  transition: all 0.4s ease;
  font-size: 1em;
  color: #fff;
  font-family: 'Bentham', serif;
  font-style: italic; }

div.backBtn:hover span.label {
  left: 2px; }

div.backBtn:hover span.line {
  left: -10px;
  height: 5px;
  background-color: #fff; }

div.backBtn:hover span.tLine {
  width: 25px;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  left: -15px;
  top: 6px; }

div.backBtn:hover span.mLine {
  opacity: 1;
  width: 30px; }

div.backBtn:hover span.bLine {
  width: 25px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  left: -15px;
  top: 20px; }

